@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,300&display=swap');

body{

    margin: 0;
    padding: 0;
    font-family:'Roboto', sans-serif; ;
   
}
.tabela input{
    outline: none;
}
.main{
    text-align: center;
    background: rgb(255, 255, 255);
    width: 100vw;
    height: 100vh;
    position: fixed;
}
.container{
    background: transparent;
    display: inline-block;
    margin-top: 100px;
}
.titulo{
    margin-top: 10px;
    font-size: 25px;
    color:#ff6600;
    letter-spacing: 4px;
}
.tabela{
    text-align: left;
    margin-top: 45px;
}
.tabela td{
    padding-top: 30px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.vendas{
    padding-top: 20px;
    border: none;
    width: 100%;
    border-bottom: 2px solid rgb(63, 175, 151);
    background: transparent;
    font-size: 20px;
}
.base-inss{
    border: none;
    border-bottom: 2px solid #bebebe;
    padding-top: 20px;
    width: 18vw;
    font-size: 20px;
    background: transparent;
}
.desconto-inss{
    border: none;
    border-bottom: 2px solid #bebebe;
    padding-top: 20px;
    width: 18vw;
    font-size: 20px;
    background: transparent;
}
.desconto-irpf{
    border: none;
    border-bottom: 2px solid #bebebe;
    padding-top: 20px;
    width: 18vw;
    font-size: 20px;
    background: transparent;
}
.sal-liquido{
    border: none;
    border-bottom: 2px solid #bebebe;
    padding-top: 20px;
    width: 18vw;
    font-size: 20px;
    background: transparent;
}
.td-vendas{
    width: 55vw;
}
.td-desc-inss{
    padding-left: 30px;
}
.td-desc-irpf{
    padding-left: 200px;
    text-align: right;
}
.label-vendas{
    font-weight: 600;
    font-size: 20px;
}
.teste1{
width: 100%;
height: 100%;
background: transparent;
text-align: left;
}

.mini-footer-tb{
    margin-top: 50px;
    width: 100%;
    height: 30px;
    background: #ff6600;
    background-image: linear-gradient(to right, #ff9721 5%, #ff5100 10%, rgb(5, 172, 144) 85%);
    border-radius: 0px;
}

.div-desc-inss{
    border: none;
    border-bottom: 2px solid #ff0000;
    padding-top: 20px;
    width: 18vw;
    height: 20px;
    font-size: 20px;
    background: transparent;
    color: #ff0000;
    margin: 0;
    
}
.ue{
    z-index: 100;
    width: 10vw;
    height: 2vw;
    background-color: #ff0000;
    color: aqua;
}

@media(max-width: 457px)
{
    table .vendas{
        width: 97vw;
    }
    table .label-inss{
        width: 30vw;
       background: transparent;
       float: right;
    }
    table .td-desc-irpf{
        padding-left: 10px;
    }

}
